@media screen and (min-width: 0px) and (max-width: 640px) {


	.header-content, .main-content,
	.main-footer-content {
		width: 100%;
	}

	.main-content .main-left-part,
	.main-content .post-list {
		width: 100%;
		float: none;
	}

	.main-content .post-list .post-wrap {
		padding: 20px;
	}

	.post .post-category,
	.post .post-comment,
	.post .post-meta,
	.post .tag-list,
	.post .post-time {
		float: none;
		width: auto;
		margin: 10px 0;
		display: block;
	}

	.single-post-wrap .post-thumbnail-wrap .thumbnail {
		width: 100%;
	}

	.entry p {
		text-indent: 0;
	}

	.post .author-avatar-wrap {
		display: none;
	}

	.main-right-aside {
		float: none;
		width: 100%;
		margin-left: 0;
		margin-top: 20px;
	}
